import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col } from 'antd';
import { getListDiscipline } from 'services/AddDiscipline';
import { getQueryParams } from 'utils/urlUtil';
import FormFilterDiscipline from './FormFilterDiscipline';

class AddDisciplinePage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
      reload: this.state.reload + 1,
    });
  };
  render() {
    const { reload, paramSearchClass } = this.state;
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số QĐ',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngày QĐ',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
      },
      {
        title: 'Cấp kỷ luật',
        dataIndex: 'parentBonusTypeLabel',
        key: 'parentBonusTypeLabel',
      },
      {
        title: 'Nội dung vi phạm',
        dataIndex: 'disciplineBehaviorLabel',
        key: 'disciplineBehaviorLabel',
      },
      {
        title: 'Hình thức xử lý',
        dataIndex: 'disciplineLabel',
        key: 'disciplineLabel',
      },
      {
        title: 'Nội dung xử lý',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#discipline/updateForm/${record.approvalId}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
            hidenBtnDelete="true"
          />
        ),
      },
    ];
    return (
      <Card title="Nhập kỷ luật">
        <Row>
          <Col span={24}>
            <FormFilterDiscipline dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/discipline/addForm" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getListDiscipline}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddDisciplinePage />
  </Page>
);



